/** @jsx jsx */
/**
 * Layout component that queries for data
 * with Gatsby's useStaticQuery component
 *
 * See: https://www.gatsbyjs.org/docs/use-static-query/
 */
import { jsx, css } from '@emotion/core';
import { ReactNode } from 'react';
import { Container } from 'react-bootstrap';
import { Color } from 'src/constants';
import Header from 'components/Header';
import Footer from 'components/Footer';
import SEO from 'components/seo';

interface Props {
  children: ReactNode;
  pageName: string;
  showHead?: boolean;
  description?: string;
}
const Page = ({ children, pageName, showHead = true, description }: Props) => {
  return (
    <div data-layout="page">
      <SEO title={pageName} />
      <Header />
      <div data-page={pageName}>
        <Container css={css`
          margin-top: 3rem;
          margin-bottom: 3rem;
        `}>
          {showHead && (
            <h1 css={css`
              color: ${Color.BlueGrey900};
            `}>
              {pageName}
            </h1>
          )}
          {showHead && description && (
            <p>{description}</p>
          )}
        </Container>
        <main>
          {children}
        </main>
      </div>
      <Footer />
    </div>
  )
};

export default Page;
