/** @jsx jsx */
import { jsx, css } from '@emotion/core';
import styled from '@emotion/styled';
import { Link } from 'gatsby';
import { Row, Col } from 'react-bootstrap';
import { GlobalMenus, Color } from 'src/constants';
import { Media } from 'src/utils';
import LogoSvg from 'src/images/logos/logo.svg';
import LogoWhitePng from 'src/images/logos/logo_white.png';


interface Props {
  logoColor: Color;
}
const Header = ({ logoColor = Color.Black }) => {
  const logoImage = logoColor === Color.Black ? LogoSvg : LogoWhitePng;
  return (
    <header css={css`
      padding: 1rem;
    `}>
      <Row>
        <Col xs="auto">
          <Link css={css`
            display: flex;
            align-items: center;
          `} to="/">
            <Logo src={logoImage} alt="Lubycon 로고" />
            <LubyconName>Lubycon</LubyconName>
          </Link>
        </Col>
        <Col>
          <Menus>
            {GlobalMenus.map((menu, index) => {
              return (
                <Menu key={index}>
                  {menu.externalLink ? (
                    <a href={menu.link} target="_blank" rel="noopener noreferrer">{menu.name}</a>
                  ) : (
                    <Link to={menu.link}>{menu.name}</Link>
                  )}
                </Menu>
              );
            })}
          </Menus>
        </Col>
      </Row>
    </header>
  )
};

const Logo = styled.img`
  width: 30px;
  margin: 0 .5rem 0 0;
  vertical-align: middle;
`;
const LubyconName = styled.span`
  color: ${Color.Black};
  font-size: 1rem;
  ${Media.mobile} {
    display: none;
  }
`;
const Menus = styled.ul`
  display: flex;
  padding: 0;
  margin: 0;
  align-items: center;
  height: 100%;
`;
const Menu = styled.li`
  font-size: 1rem;
  list-style: none;
  margin-right: 1rem;
  ${Media.mobile} {
    margin-right: .5rem;
  }
  &:hover {
    opacity: .6;
  }
  &::after {
    margin-left: 1rem;
    content: '|';
    ${Media.mobile} {
      margin-left: .5rem;
    }
  }
  &:last-child {
    margin-right: 0;
    &::after {
      margin-left: 0;
      content: '';
    }
  }
  a {
    color: ${Color.BlueGrey600};
    text-decoration: none;
    ${Media.mobile} {
      font-size: .7rem;
    }
  }
`;

export default Header;
